.projects {
    color: var(--blue_gray-100);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
}

.list {
    border: 2px solid hotpink;
    display: flex;
    padding: 0;
    gap: 50px;
    width: 100%;
    overflow-x: auto;
}

.list li {
    border: 2px solid red;
    list-style: none;
}